header {
	position: sticky;
	top: 0;
	z-index: 100;
	background: $white;
	border-bottom: 2px solid $primary;
}

.navbar {
	padding:2rem;
	display: grid;
	grid-auto-flow: row;
	gap: 1rem;
	align-content: flex-start;
	justify-content: space-between;
	align-items: center;

	position: fixed;
	right: -500px;
	height: 100vh;
	width: 50vw;
	min-width: 300px;
	max-width: 500px;
	z-index: 100;
	background: $white;
	transition: right ease-in-out 0.4s;

	&.show { right: 0; }
}

.navbar-brand { height: 48px; }

.navbar-nav {
	display: grid;
	grid-auto-flow: row;
	gap: 1.5rem;
	align-items: center;
	justify-content: center;

	&.secondary {
		padding-top: 1rem;
		grid-auto-flow: column;
		gap: 1rem;

		.nav-item:not(:last-child) {
			padding-right: 1rem;
			border-right: 2px solid $primary;
		}
	}
}

