		}

		&:hover::after { background-color: rgba($primary, 0.8); }
	}
}

.nav-column {
	display: grid;
	grid-auto-flow: row;
	gap: 1rem;
}

.navbar-toggler {
	padding: 1rem 1rem 1rem 0;
	display: grid;
	grid-auto-flow: column;
	justify-content: space-between;
	align-items: center;
}

main::before {
	content: '';
	position: fixed;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	z-index: 10;
	visibility: hidden;
	background: rgba($dark, 0);
	transition: all ease-in-out 0.2s;
	mix-blend-mode: multiply;
}

main.backdrop::before { visibility: visible; background: rgba($dark, 0.7); }